@import '~bootstrap/scss/bootstrap';
@import './abstracts/variables';

::-webkit-scrollbar {
    width: 0%;
}
body {
    background-color: $main-color;
    color: $second-color;
    height: 100vh;
    width: 100vw;
    margin: 0%;
    font-family: Satoshi;
    font-size: 75%;
    cursor: none;
}
.skip-container {
    margin-top: 1%;
    display: flex;
    position: absolute;
    justify-content: flex-start;
    align-items: center;
    right: 1%;
    padding-left: 35px;
    width: fit-content;
    cursor: none;
    font-size: small;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    z-index: 999;
    input {
        position: absolute;
        opacity: 0;
        cursor: none;
        height: 0px;
        width: 0px;
    }
    .checkmark {
        position: absolute;
        transition: all 0.5s linear;
        box-shadow: inset 0px 0px 0px 0px $second-color-shade;
        top: 0;
        left: 0;
        height: 20px;
        width: 20px;
        background-color: $main-color-shade;
    }
}

.skip-container:hover input ~ .checkmark {
    cursor: none;
    background-color: $main-color-shade-2;
}

.skip-container input:checked ~ .checkmark {
    cursor: none;
    box-shadow: inset 0px 0px 0px 100px $second-color-shade;
}

h1 {
    margin: 0%;
}
h2 {
    margin: 2em;
}
a {
    text-decoration: none;
    color: $accent-color;
    cursor: none;
    &:hover {
        color: $accent-color;
        cursor: none;
    }
}

nav {
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 5%;
    display: grid;
    gap: 15px;
    width: 0;
    justify-content: center;
    align-content: center;
    z-index: 1;

    a {
        transition: all 0.5s linear;
        color: $accent-color;
        text-decoration: none;
        font-weight: 500;
        cursor: none;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: auto;
        &:hover {
            > .text-link {
                opacity: 1;
                transform: translate3d(40px, 0, 0);
            }
        }
    }
}
nav {
    a.active {
        .nav-dot {
            background-color: $accent-color;
            transition: 0.3s;
            transform: scale(2);
        }
        .text-link {
            opacity: 1;
            transform: translate3d(40px, 0, 0);
        }
    }
}
.nav-line-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10vh;
}
.nav-dot {
    position: absolute;
    background-color: $accent-color;
    border-radius: 50%;
    width: 5px;
    height: 5px;
}
.nav-line {
    width: 1px;
    height: 100%;
    background-color: $accent-color;
}
.text-link {
    position: relative;
    color: $accent-color;
    transition: all 0.2s linear;
    padding-right: 1%;
    opacity: 0;
    font-size: 120%;
    user-select: none;
}
.intro-panel {
    position: relative;
    transition: all 0.5s linear;
    background-color: $main-color;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 97%;
    &::after {
        transition: all 0.5s ease-in;
        content: '';
        position: absolute;
        top: 0px;
        left: 0px;
        width: 0;
        height: 5px;
        background-color: $accent-color;
    }
    &::before {
        transition: all 1s ease-in;
        content: '';
        position: absolute;
        bottom: 0px;
        right: 0px;
        width: 0;
        height: 5px;
        background-color: accent-color;
    }
}

.starting-btn {
    user-select: none;
    position: relative;
    width: 10%;
    z-index: 1;
    min-width: 100px;
    height: 5em;
    font-weight: 700;
    border: none;
    background-color: $main-color;
    color: $second-color;
    transition: all 0.5s ease-in;
    &:hover {
        background-color: $second-color;
        color: $main-color;
        cursor: none;
        &::after {
            width: 100%;
            background-color: $second-color;
        }
        &::before {
            width: 100%;
            background-color: $second-color;
        }
    }
    &::after {
        content: '';
        position: absolute;
        top: 0px;
        left: 0px;
        width: 10px;
        height: 8%;
        background-color: $second-color;
        transition: all 0.3s linear;
    }
    &::before {
        content: '';
        position: absolute;
        bottom: 0px;
        right: 0px;
        width: 10px;
        height: 8%;
        background-color: $second-color;
        transition: all 0.3s linear;
    }
}
.welcome {
    position: fixed;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    font-weight: 700;
    font-size: 150%;
    text-align: center;
    z-index: 0;
    white-space: nowrap;
}
.intro-panel-h1 {
    position: fixed;
    user-select: none;
    color: $second-color;
    font-weight: 700;
    text-align: center;
    font-size: 150%;
    z-index: 0;
    white-space: nowrap;
}
.first-panel {
    display: flex;
    flex-direction: column;
    margin-bottom: 5vh;
    height: 100%;
    width: 100%;
    font-weight: 700;
    font-size: 200%;
    justify-content: center;
    align-items: flex-end;
    left: 30%;
    gap: 5%;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.name-heading {
    margin-right: 30%;
    text-align: right;
    font-size: 200%;
}
.name {
    position: relative;
    font-size: 110%;
}
#contact-btn-a {
    margin-right: 30%;
}
.contact-btn {
    user-select: none;
    cursor: none;
    border: none;
    background-color: $main-color;
    color: $second-color;
    padding: 10px 30px 10px 30px;
    box-shadow: 0.3em 0.3em 0 0 $second-color, inset 0.3em 0.3em 0 0 $second-color;
    transition: all 0.2s linear;
    &:hover,
    &:focus {
        cursor: none;
        box-shadow: 0 0 0 0 $second-color, inset 6em 3.5em 0 0 $second-color;
        color: $main-color;
    }
}
.second-panel {
    display: flex;
    margin-bottom: 5vh;
    flex-direction: column;
    height: 100%;
    width: 100%;
    padding: 10%;
    justify-content: center;
    align-items: center;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}
.second-panel-content {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}
@media screen and (max-width: 755px) {
    .second-panel-content {
      flex-direction: column;
      align-items: center;
      gap: 2vh;
    }
    .projects-cards{
        flex-direction: column;
        gap: 30px;
    }
    .form {
        padding-left: 5%;
        padding-right: 5%;
    }
  }
.card {
    background-color: $second-color;
    border: none;
    width: 14rem;
    cursor: none;
    margin-right: 20%;
    margin-left: 20%;
}
.card-img-top {
    mask-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 1)), to(rgba(0, 0, 0, 0.747)));
    box-shadow: 0px 0px 24px -9px white;
}
.card-body {
    backdrop-filter: opacity(10%);
    box-shadow: 0px 0px 24px -9px $second-color;
}
.card-title,
.card-text {
    color: $main-color;
}
.buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2px;
}
.play-btn {
    text-decoration: none;
    background-color: $accent-color;
    border-radius: 50%;
    width: 30px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s linear;
    aspect-ratio: 1/1;
}
.play-btn:hover {
    transform: scale(1.05);
    cursor: none;
}
.play-btn::after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-left: 8px solid $main-color;
}

.skip-btn {
    text-decoration: none;
    width: 5px;
    height: 16px;
    border: none;
    border-right: 3px solid $accent-color;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.skip-btn::before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    border-left: 8px solid $accent-color;
}
.progress-bar-div {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.previous-btn {
    transform: rotate(180deg);
}
.progress {
    background-color: $accent-color;
    margin: 2%;
    height: 0.5vh;
    width: 80%;
}
.progress-bar {
    background-color: $main-color;
    border-radius: 5px;
}
label[for='progress'] {
    color: black;
    font-size: smaller;
}
.about-me-div {
    width: 40%;
    margin-right: 10%;
}
.about-me-p {
    font-size: medium;
}
.third-panel {
    display: flex;
    margin-bottom: 5vh;
    flex-direction: column;
    height: 70%;
    padding-left: 15%;
    padding-right: 15%;
    justify-content: center;
    align-items: center;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    .card{   
        width: 150pt;
        height: fit-content;
        margin: 0;
    }
    .card-text{
        font-size: 1.2em;
    }
    .btn{
        background-color: $main-color-shade-2;
    }
    .check-btn {
        user-select: none;
        cursor: none;
        border: none;
        background-color: $main-color;
        color: $accent-color;
        padding: 10px 30px 10px 30px;
        box-shadow: 0.3em 0.3em 0 0 $accent-color, inset 0.3em 0.3em 0 0 $accent-color;
        transition: all 0.2s linear;
        font-size: 1.2em;
        &:hover,
        &:focus {
            cursor: none;
            box-shadow: 0 0 0 0 $accent-color, inset 6em 3.5em 0 0 $accent-color;
            color: $main-color;
        }
    }
}
.projects-cards{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: auto;
}
.projects-p {
    font-size: medium;
}
.card-body[for="project"]{
background-color: red;
}
.fourth-panel {
    display: flex;
    margin-bottom: 5vh;
    flex-direction: column;
    height: 100%;
    padding: 10%;
    justify-content: center;
    align-items: center;
    user-select: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}
.fourth-panel {
    > form {
        padding-left: 25%;
        padding-right: 25%;
        margin: 0%;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;

        .container {
            width: 100%;
            padding: 0%;
        }
        input,
        input:focus,
        textarea,
        textarea:focus {
            background-color: $main-color-shade;
            border: none;
            cursor: none;
            color: $accent-color;
            outline: none;
            overflow: auto;
            height: 100%;
            width: 100%;
            padding: 1.8%;
            border-radius: 0.375rem;
        }
        textarea::placeholder {
            color: $second-color;
        }

        label {
            margin-left: 1vw;
        }
        .row {
            margin: 0%;
            margin-top: 2%;
        }

        .mail-submit-btn {
            user-select: none;
            cursor: none;
            border: none;
            position: relative;
            background-color: $main-color;
            color: $second-color;
            padding: 1vh 1.5vw 1vh 1.5vw;
            outline: none;
            border-radius: 0px;
            box-shadow: 0.3em 0.3em 0 0 $second-color, inset 0.3em 0.3em 0 0 $second-color;
            transition: all 0.2s linear;
            &:hover,
            &:focus {
                cursor: none;
                box-shadow: 0 0 0 0 $second-color, inset 6em 3.5em 0 0 $second-color;
                color: $main-color;
            }
        }
    }
}
textarea{
    font-size: 1.3em;
}
textarea::placeholder{
    font-size: .9em;
}
footer {
    transition: all 0.5s linear;
    width: 100%;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin-bottom: 10px;
    background-color: $main-color;
    font-size: 90%;
    font-weight: 700;
    text-align: center;
    height: 3%;
    position: fixed;
    padding-top: 15px;
    z-index: 1;
}

.padding {
    padding: 0;
}
@media only screen and (max-device-width: 480px) {
    .nav-links {
        display: none;
    }
}
.cursor {
    mix-blend-mode: difference;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    border: 1px solid $second-color;
    transition: all 200ms ease-out;
    position: fixed;
    pointer-events: none;
    left: 0;
    top: 0;
    transform: translate(calc(-50% + 15px), -50%);
    transition: width 0.3s, height 0.3s;
    z-index: 1001;
}

.cursor2 {
    mix-blend-mode: difference;
    width: 15px;
    height: 15px;
    border-radius: 100%;
    background-color: $second-color;
    opacity: 0.3;
    position: fixed;
    transform: translate(-50%, -50%);
    pointer-events: none;
    transition: width 0.3s, height 0.3s, opacity 0.3s;
    z-index: 1001;
}
.inner-circle-hover {
    width: 25px;
    height: 25px;
}
.mouse-down {
    width: 0px;
    height: 0px;
}
.mouse-up {
    width: 30px;
    height: 30px;
}
.mouse-click {
    animation: mouse-click 0.5s linear;
}
@keyframes mouse-click {
    0% {
        width: 30px;
        height: 30px;
    }
    25% {
        width: 15px;
        height: 15px;
    }
    50% {
        width: 0px;
        height: 0px;
    }
    75% {
        width: 15px;
        height: 15px;
    }
    100% {
        width: 30px;
        height: 30px;
    }
}
.hide {
    opacity: 0;
}
.show {
    opacity: 100;
}
.opacity {
    opacity: 0;
}
.height {
    height: 0;
}
